.nav-tabs,
.nav-tabs-tools,
.nav-tabs-filter,
.nav-tabs-api,
.nav-tabs-apiauth {
  float: left;
  width: 100%;
  margin: 0;
  list-style-type: none;
  border-bottom: 1px solid transparent;

  > li {
    float: left;
    margin-bottom: -1px;

    > a {
      margin-right: 2px;
      line-height: 1.5;
      padding: 10px;
      border: 1px solid transparent;
      border-radius: 4px 4px 0 0;
      float: left;
      text-decoration: none;

      &:hover {
        border-color: #eee #eee #ddd;
      }
    }

    &.active > a {
      &,
      &:hover,
      &:focus {
        color: #555;
        cursor: default;
        background-color: #fff;
        border-color: transparent;
      }
    }
  }
}

.tab-content > .tab-pane,
.tab-content-tools > .tab-pane-tools {
  float: left;
  width: 98%;
  display: none;

  &.active {
    display: block;
    padding: 10px;
    background-color: #fff;
    box-shadow: 0 5px 4px -2px rgba(0, 0, 0, 0.15);
  }
}

#top-btn {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: purple;
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 4px;
}

#top-btn:hover {
  background-color: #555;
}
